import React from 'react'
import { Header, Image, Button, Container, Grid } from 'semantic-ui-react'
import { navigateTo } from 'gatsby-link'
import Simple from '../../components/simple.js'
import img from '../../data/img/pm.png'
export default (props) => (
  <Simple 
  title={'Our Tips'}
    description={
      <Container>
        <p> In these cases, we suggest a password manager </p>
        <p> Password managers are what we think best balance practicality with security.
        You only need to remember a master password to your password manager and it will take care of creating distinct, complex passwords any time you need them.
        </p>
      </Container>
    }
    to='/recommendation/recap/'
    buttonText={'Next'}
   >
    <Grid.Row>
      <Grid.Column width={14}>
        <Image src={img} fluid />
      </Grid.Column>
    </Grid.Row>
  </Simple>
)



